<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('PostComment') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        PostId: {
            type: Number,
            required: true
        },
        resultData: {
            type: Object,
            required: true
        },
        PostStatus: {
            type: String,
            required: true
        },
        AllPages: {
            type: Object,
            required: true
        },
        CommentType: {
            type: String,
            required: true
        }, 
        CommentId: {
            type: Number,
            required: true
        }, 
        ApproverId: {
            type: Number,
            required: true
        },  
    },
    data() {
        return {
            isLoading: false,
            fileData: [],
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Title'),
                            name: "TitleName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('Visibility'),
                            name: "Visibility",
                            value: "1",
                            config: {
                                options: [
                                    {
                                        name: 'Public', value: "1"
                                    }
                                    , {
                                        name: 'Private', value: "0"
                                    }
                                ]
                            },
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('description'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('EnterDescription'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-paper-plane pr-2'></i>Post New Comment",
                },
                {
                    type: "button",
                    class: "btn btn-success",
                    text: "<i class='fa fa-paper-plane pr-2'></i>Update Post",
                    onClick: this.UpdatePost
                },
            ],
        }
    },
    created: async function () {
        var vm=this;
        vm.UserId = vm.GetUserInfo.ID;
        if(vm.UserId==vm.ApproverId){
            vm.buttons[1].isVisible= false;
        }else{
            vm.buttons[0].isVisible= false;
        }
        if(vm.CommentId>0){
        this.FetchData();
        }
    },
    methods: {
        FetchData: function () {
            var vueObj = this;
            var url = `id=${vueObj.CommentId}`;
            DataServices.GetPostCommentData (url).then(
                function (response) {
                    if (response.data != null && response.data != "") {
                        if (response.data.data.length >0) {
                            (vueObj.FormSchema)[0].Data[0].value = response.data.data[0].title;
                            (vueObj.FormSchema)[0].Data[1].value = response.data.data[0].is_visibility;
                            (vueObj.FormSchema)[1].Data[0].value = response.data.data[0].Comment;
                        }
                    }
                }
            );
        },
        Close: function () {
            this.$parent.PostComment = false;
        },
        UpdatePost: function () {
            var vm= this;
            this.$router.push({ name: "AddSocialMediaPost", params: { id: vm.PostId } });
        },
        onSubmit(formfields, val) {
            var vm = this;
            if(vm.CommentType=='SendForCorrection'){
                var SendForCorrection=1;
            }else{
                var SendForCorrection=0;
            }
            var jsonobj = {
                id: vm.CommentId,
                post_approver_id: vm.PostId,
                title: formfields.TitleName,
                comment: formfields.Description,
                is_visibility: formfields.Visibility,
                is_send_for_correction: SendForCorrection,
                is_corrected: 0,
                status_id: 1001
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SavePostComment(params).then((response) => {
                if (response.data.MSG == 'Insert Success' || response.data.MSG == 'Updated Success') {
                    if (vm.PostStatus == 'CRM_APPROVE' || vm.PostStatus == 'CRM_REJECT') {
                        var postJSON = {
                            ids: vm.PostId,
                            moduleName: "CRM",
                            subModuleCode: "CRM_APPROVAL_PENDING",
                            approvaltype: vm.PostStatus
                        };
                        var objectPostString = JSON.stringify(postJSON);
                        var currentDate = new Date();
                        var startDate = vm.resultData.start_date;
                        var startTime = vm.resultData.start_time;
                        var startDateTimeString = startDate + 'T' + startTime;
                        var startDateTime = new Date(startDateTimeString);
                        var currentDate = new Date();
                        if (currentDate <= startDateTime) {
                            DataServices.ApproveRejectCampaign(objectPostString).then(function (response) {
                                var box = "";
                                if (response.data == 0) {
                                    $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                    box = vm.ShowAlert(vm.$t("RecordUpdatedSuccessfully"), "success", vm.$t('Alert'));
                                    vm.$parent.OpenPostModal = false;
                                    vm.Close();
                                    if (vm.PostStatus == 'CRM_APPROVE') {
                                        if (response.statusText=='OK' || response.status==200) {
                                            var fileObj = {
                                                attachement_mime: vm.resultData.attachement_mime,
                                                fileName: vm.resultData.attachement_name,
                                                attachement_path: vm.resultData.attachement_path
                                            };
                                            vm.fileData.push(fileObj);
                                            var obj = {
                                                ref_page_id: vm.resultData.ref_page_id,
                                                campaign_owner_id: vm.resultData.campaign_owner_id,
                                                content: vm.resultData.content,
                                                followup_content: vm.resultData.followup_content,
                                                category_id: vm.resultData.category_id,
                                                tag_id: vm.resultData.tag_id,
                                                is_draft: false,
                                                schedule_date: vm.resultData.start_date ? vm.$options.filters.formatDate(vm.resultData.start_date) : vm.resultData.start_date,
                                                schedule_time: vm.resultData.start_time ? vm.$options.filters.formatTime(vm.resultData.start_time) : vm.resultData.start_time,
                                                schedule: [],
                                                approver: [],
                                                attachments: vm.fileData
                                            }
                                            var url = JSON.stringify(obj);
                                            let accountForPost = vm.AllPages.filter(x => x.PAGE_ID == obj.ref_page_id)[0].ACCOUNT_NAME;
                                            if (accountForPost) {
                                                if (accountForPost.toLowerCase() == 'facebook') {
                                                    DataServices.UploadFacebookPost(url).then(function (response) {
                                                    })
                                                } else if (accountForPost.toLowerCase() == 'instagram') {

                                                }
                                            }
                                        }
                                    }
                                    vm.$parent.PostStatus='';
                                    vm.$parent.FetchData();
                                    $('.statuschange').addClass('disabled');
                                }
                                else {
                                    box = vm.ShowAlert(vm.$t("Error Occur while Approving Record"), "warning", true, vm.$t("Alert"));
                                }
                            }, function (response) {
                            });
                        } else {
                            vm.ShowAlert(vm.$t("ApprovalTimeExpired"), "warning", true, vm.$t("Alert"));
                        }
                    } else {
                        if(vm.CommentId>0){
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Comment")]), "success", true, vm.$t("Alert"));
                            vm.$parent.FetchData();
                            vm.Close();
                        }else{
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Comment")]), "success", true, vm.$t("Alert"));
                        vm.$parent.FetchData();
                        vm.Close();
                    }
                    }
                }
            });
        }
    }
}
</script>