<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" ModuleName="CRM" SubModuleCode="CRM_TRIGGER_LINK" :RenderRowActionMethod="renderActions" 
        v-on:ActionButtonClick="actionButtonClick" IdentityColumn="TRIGGER_ID" :HeaderText="$t('Integration')" :listType="['List']" 
        :ListData="IntegrationData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecord" 
        :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp" 
        :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch" :CurrentPageNumber="PageNumber"
        v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata"></tg-list>
        <modaladdsmtp v-if="ShowModalAddSMTP" @close="ShowModalAddSMTP=false"></modaladdsmtp>
        <modalManageAdditionalSMTP v-if="ShowmodalManageAdditionalSMTP" :reqFrom="reqFrom" @close="ShowmodalManageAdditionalSMTP=false"></modalManageAdditionalSMTP>
    </section>
</template>

<script>
import Schema from '../IntegrationScreen/IntegrationScreenSchema';
import modaladdsmtp from '../../../components/Common/Modal/modaladdsmtp.vue';
import modalManageAdditionalSMTP from '../../../components/Common/Modal/modalManageAdditionalSMTP.vue';

export default {
    components: {
        modaladdsmtp,
        modalManageAdditionalSMTP
    },
    data() {
        return {
            ShowmodalManageAdditionalSMTP: false,
            reqFrom:'',
            isLoading: false,
            ShowModalAddSMTP: false,
            IntegrationData: [],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            TotalRecord: 0,
            SortExp: '',
            SortBy: '',
            PageNumber: 1,
            PageSize: 10,
            privilegeParams: null,
            TotalPages: 0,
            CurrentPage: 1,
            Headers: [{
                    COLUMN_NAME: "",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Sender Name",
                    DISPLAY_ORDER: 1
                },
                {
                    COLUMN_NAME: "",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Profile Name",
                    DISPLAY_ORDER: 2
                }
            ]
        };
    },
    created() {

    },
    methods: {
        OpenmodalManageAdditionalSMTP: function(reqFrom){
            this.reqFrom=reqFrom;
            this.ShowmodalManageAdditionalSMTP = true;
        },
        ClosemodalManageAdditionalSMTP: function(){
            this.reqFrom='';
            this.ShowmodalManageAdditionalSMTP = false;
        },
        OpenModalAddSMTP: function (){
            this.ShowModalAddSMTP= true;
        },
        CloseModalAddSMTP: function(){
            this.ShowModalAddSMTP = false;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (s) {
          var SortDir = "sort tb_headerSortDown";
           this.SortBy = s;
                if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else
                {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                }
                this.FetchData();
                $("#" + s+'  span:first-child').attr("class", SortDir);
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "OpenModalAddSMTP":
                    this.OpenModalAddSMTP();
                    break;
                case "ManageMailChimp":
                    this.OpenmodalManageAdditionalSMTP('MailChimp');
                    break;
                case "ManageSendGrid":
                    this.OpenmodalManageAdditionalSMTP('SendGrid');
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        actionButtonClick: function (callbackfunction, item) {
           var vm = this;
            switch (callbackfunction) {
                case 'OpenModalAddSMTP':
                    vm.OpenModalAddSMTP(item);
                    break;
                case 'Delete':
                    vm.commonDeleteData();
                    break;
            }
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        FetchData: function () {
           var vm = this;
          
        }
    },
}
</script>