<template>
    <section>
    <loader :is-visible="isLoading"></loader>
    <tg-list :IsShowAction="true" ModuleName="CRM" SubModuleCode="CRM_SMS_TRIGGER" :RenderRowActionMethod="renderActions" 
    v-on:ActionButtonClick="actionButtonClick" IdentityColumn="TRIGGER_ID" :HeaderText="$t('TriggerLink')" :listType="['List']" 
    :ListData="TriggerData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecord" 
    :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp" 
    :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch" :CurrentPageNumber="PageNumber"
    v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata"></tg-list>
    <modalManageTriggerLink v-if="ShowmodalManageTriggerLink" :Data="TriggerLinkData" @close="ShowmodalManageTriggerLink=false"></modalManageTriggerLink>
    <modalTriggerLinkStatistics v-if="ShowmodalTriggerLinkStatistics" @close="ShowmodalTriggerLinkStatistics=false"></modalTriggerLinkStatistics>
    </section>
</template>

<script>
import Schema from '../TriggerLink/TriggerLinkSchema';
import CampaignDataServices from '../../../services/DataService';
import modalManageTriggerLink from '../../../components/Common/Modal/modalManageTriggerLink.vue';
import modalTriggerLinkStatistics from '../../../components/Common/Modal/modalTriggerLinkStatistics.vue'

export default {
    components: {
        modalManageTriggerLink,
        modalTriggerLinkStatistics
    },
    data() {
        return {
            ShowmodalManageTriggerLink: false,
            ShowmodalTriggerLinkStatistics: false,
            isLoading: false,
            TriggerData: [],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            TotalRecord: 0,
            SortExp: '',
            SortBy: '',
            PageNumber: 1,
            PageSize: 10,
            privilegeParams: null,
            TotalPages: 0,
            CurrentPage: 1,
            TriggerLinkData:null,
            Headers: [{
                    COLUMN_NAME: "TRIGGER_TITLE",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Name",
                    DISPLAY_ORDER: 1
                },
                {
                    COLUMN_NAME: "TRIGGER_LINK",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Link URL",
                    DISPLAY_ORDER: 2
                }
            ]
        };
    },
    created() {
        this.FetchData();
        this.privilegeParams = `controller=CRM&action=`;
    },
    methods: {
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (s) {
          var SortDir = "sort tb_headerSortDown";
           this.SortBy = s;
                if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else
                {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                }
                this.FetchData();
                $("#" + s+'  span:first-child').attr("class", SortDir);
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "modalManageTriggerLink":
                    this.modalManageTriggerLink();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "setTourGuideSteps":
                    this.setTourGuideSteps();
                    break;
            }
        },
        actionButtonClick: function (callbackfunction, item) {
            var vm = this;
            switch (callbackfunction) {
                case 'modalManageTriggerLink':
                    vm.modalManageTriggerLink(item);
                    break;
                case 'Delete':
                    vm.commonDeleteData(item.TRIGGER_ID);
                    break;
                case 'modalTriggerLinkStatistics':
                    vm.modalTriggerLinkStatistics();
                    break;    
            }
        },
        modalTriggerLinkStatistics: function(){
            this.ShowmodalTriggerLinkStatistics = true;
        },
        ClosemodalTriggerLinkStatistics: function(){
            this.ShowmodalTriggerLinkStatistics = false;
        },
        modalManageTriggerLink: function (item) {
            this.TriggerLinkData=item;
            this.ShowmodalManageTriggerLink = true;
        },
        ClosemodalManageTriggerLink: function () {
            this.ShowmodalManageTriggerLink = false;
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        FetchData: function () {
           var vm = this;
           vm.isLoading = true; 
           var url = `triggerlink=${vm.trigger_title}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
           CampaignDataServices.GetTriggerLinkListing(url).then(response=>{
            vm.TriggerData=[];
            if(response.data !=''){
                vm.TriggerData = response.data.data;
                vm.TotalRecord = response.data.data[0].Total;
                vm.TotalPages = Math.ceil(vm.TotalRecord / vm.PageSize);
                vm.CurrentPage = vm.PageNumber;
                vm.noRecord = false;
            }
           });
           setTimeout(function () {
                vm.ResponsiveDataTable('tablelistingdata')
            }, 500);
            vm.isLoading = false;  
        },
        commonDeleteData: function (TRIGGER_ID) {
            var vm = this;
            CampaignDataServices.CheckPrivilege(vm.privilegeParams + 'Delete')
                .then(function (response) {
                    if (response.data) {
                        var val = "";
                        if (typeof (TRIGGER_ID) != 'undefined') {
                            val = TRIGGER_ID.toString();
                        } else {
                            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                                if (val.length > 0)
                                    val += ',';
                                val += $(this).val();
                            });
                        }
                        if (val.length > 0) {
                            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Campaign")), function () {
                                var postJSON = {
                                    ids: val,
                                    moduleName: "CRM",
                                    //subModuleCode: "CRM_SMS_TRIGGER",
                                    refCode: "CRM_SMS_TRIGGER"
                                };
                                var objectPostString = JSON.stringify(postJSON);
                                CampaignDataServices.CommonDeleteData(objectPostString).then(function (response) {
                                    var box = "";
                                    if (response.status == 200) {
                                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                        $("#DeleteMultiple").addClass("disabled");
                                        box = vm.ShowAlert(vm.$t('DeletedSuccessfully', [vm.$t('Campaign')]), "success", true, vm.$t("Alert"));
                                        vm.PageNumber = parseInt(vm.PageNumber) - 1;
                                        if (vm.PageNumber <= 0) {
                                            vm.PageNumber = 1;
                                        }
                                        vm.FetchData();
                                    } else {
                                        box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Campaign')]), "failure", true, vm.$t("Alert"));
                                    }
                                    setTimeout(function () {
                                        box.modal('hide');
                                    }, 2000);

                                });
                            });
                        } else {
                            vm.ShowAlert(vm.$t('Selectanyrecordtodelete'), "warning", true, vm.$t("Alert"));
                        }
                    } else {
                        vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                    }

                });
        },
    },
}
</script>