<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" ModuleName="CRM" SubModuleCode="CRM_APPROVAL" :RenderRowActionMethod="renderActions"
            v-on:ActionButtonClick="actionButtonClick" IdentityColumn="id" :HeaderText="$t('CampaignApproval')"
            :listType="['List']" :ListData="ApprovalData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecord" :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData"
            :showCheckBox="true" :SortExp="SortExp" :LegendArray="LegendArray" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            :CurrentPageNumber="PageNumber" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <!-- <template v-if="data.column.COLUMN_NAME == 'status_id'">
                    <listing-status :isfor="'CRM_APPROVAL'" v-if="isCRMShowStausDrop(data.row.status_id, 'CRM') == true"
                        ref="updatestatus" v-bind:dataid="data.row.id" data-controller="CRM"
                        v-bind:statuslist="StatusListForListing" v-bind:item="data" data-action="CampaignApproval"
                        v-bind:dataselected="data.row.status_name" v-bind:callbackfunction="FetchData"
                        v-bind:isselectedvaluestring="true">
                    </listing-status>
                    <a class="inactive-disabled" v-if="isCRMShowStausDrop(data.row.status_id, 'CRM') == false">
                        <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                    </a>
                </template> -->
                <template v-if="data.column.COLUMN_NAME == 'status_id'">
                    <a class="inactive-disabled">
                        <span v-bind:class="getStatusClassList(data.row.status_name, 'status_id')">
                            {{ data.row.status_name }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'created_at'">
                    {{ $options.filters.formatDate(data.row[data.column.COLUMN_NAME]) }}
                </template>
            </template>
        </tg-list>
        <OpenModalViewPost v-if="OpenPostModal" @ApproveReject="ApproveReject" :ApproverId="ApproverId" :PostId="PostId"></OpenModalViewPost>
        <PostCommentModal v-if="PostComment" :AllPages="AllPages" :CommentId="CommentId" :ApproverId="ApproverId" :PostId="PostId" :CommentType="CommentType" :PostStatus="PostStatus" :resultData="resultData"></PostCommentModal>
    </section>
</template> 

<script>
import DataService from '../../../services/DataService';
import Schema from '../CampaignApproval/CampaignApprovalSchema';
import OpenModalViewPost from '../../../components/Common/Modal/OpenModalViewPost.vue';
import PostCommentModal from '../../../components/Common/Modal/PostCommentModal.vue';

export default {
    components: {
        OpenModalViewPost,PostCommentModal
    },
    data() {
        return {
            privilegeParams: null,
            StatusListForListing: [],
            LegendArray: [],
            OpenPostModal: false,
            PostComment:false,
            isLoading: false,
            PostId: 0,
            CommentId:0,
            ApproverId:0,
            PostStatus:'',
            CommentType:'',
            AllPages: [],
            resultData:[],
            ApprovalData: [],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            TotalRecord: 0,
            SortExp: '',
            SortBy: '',
            PageNumber: 1,
            PageSize: 10,
            TotalPages: 0,
            CurrentPage: 1,
            postContent: '',
            campaignOwnerId: '',
            status: '',
            dateto: '',
            datefrom: '',
            Headers: [{
                COLUMN_NAME: "created_at",
                DATA_TYPE: "text",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: "Date",
                DISPLAY_ORDER: 1
            },
            {
                COLUMN_NAME: "content",
                DATA_TYPE: "text",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: "Post Description",
                DISPLAY_ORDER: 2
            },
            {
                COLUMN_NAME: "created_by",
                DATA_TYPE: "text",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: "Created By",
                DISPLAY_ORDER: 3
            },
            {
                COLUMN_NAME: "status_id",
                DATA_TYPE: "text",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: "Status",
                DISPLAY_ORDER: 4
            }
            ]
        };
    },
    async created() {
        var vm = this;
        await vm.GetPageList();
        vm.UserId = vm.GetUserInfo.ID;
        vm.getStatuslistForListing("CRM_APPROVER", "en");
        vm.privilegeParams = `controller=CRM&action=`;
        vm.LegendArray = [
            {
                class: 'bg-yellow colorboxsquare',
                text: (vm.$t('PendingRequest'))
            },
            {
                class: 'bg-red colorboxsquare',
                text: (vm.$t('RejectedRequest'))
            }
        ],
            vm.Headers.forEach((column) => {
                column.settings = null;
                if (column.COLUMN_NAME == 'status_id' || column.COLUMN_NAME == 'created_at') {
                    column.settings = {
                        isInSlot: true,
                        isForTag: true
                    }
                }
            });
        await vm.FetchData();
        await Schema.bindleftCommonSearchdropdown(vm, "CRM", "CAMPAIGN_OWNER_SEARCH");
    },
    methods: {
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "approve":
                    this.ApproveReject('CRM_APPROVE','');
                    break;
                case "reject":
                    this.ApproveReject('CRM_REJECT', '');
                    break;
            }
        },
        actionButtonClick: function (callbackfunction, item) {
            var vm = this;
            switch (callbackfunction) {
                case 'OpenModalAddSMTP':
                    vm.OpenModalAddSMTP();
                    break;
                case 'Delete':
                    vm.commonDeleteData();
                    break;
                case 'ViewPost':
                    vm.OpenModalViewPost(item.id, item.approver_id);
                    break;
                 case 'PostCommentModal':
                    vm.PostCommentModal(item.id,'', item.comment_id, item.approver_id);
                    break;
                case 'SendForCorrection':
                    vm.PostCommentModal(item.id,'SendForCorrection', item.comment_id,item.approver_id);
                    break;
                case 'ViewCorrection':
                    vm.PostCommentModal(item.id, 'ViewCorrection', item.comment_id, item.approver_id);
                    break;
            }
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Send for Correction':
                        if(row.approver_id==self.UserId){
                            action.isVisible = true;
                            // if(row.is_send_for_correction==true && row.comment_id>0){
                            //     action.icon='fa fa-send disabled';
                            // }else{
                            //     action.icon='fa fa-send';
                            // }
                        }else{
                            action.isVisible = false;
                        }
                    break;
                    case 'View Correction':
                        if(row.approver_id==self.UserId){
                            action.isVisible = false;
                        }else{
                            action.isVisible = true ;
                            // if(row.is_corrected==true && row.comment_id>0){
                            //     action.icon='fa fa-eye';
                            // }else{
                            //     action.icon='fa fa-eye disabled';
                            // }
                        }
                    break;
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        OpenModalViewPost: function (id,ApproverId) {
            if (id == undefined)
                id = 0;
            this.PostId = id;
            this.ApproverId=ApproverId;
            this.OpenPostModal = !this.OpenPostModal;
        },
        PostCommentModal: function (id, type, CommentId, ApproverId) {
            var vm= this;
            if (id == undefined)
                id = 0;
            this.PostId = id;
            this.CommentId=CommentId;
            this.ApproverId=ApproverId;
            if(type='SendForCorrection'){
                vm.CommentType='SendForCorrection';
            }else if(type='ViewCorrection'){
                vm.CommentType='ViewCorrection';
            }
            this.PostComment = !this.PostComment;
        },
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
            DataService.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Approved':
                    returnClass = 'px-2 bg-primary text-center text-white d-block';
                    break;
                case 'Approval Pending':
                    returnClass = 'px-2 bg-info text-center text-white d-block';
                    break;
                case 'Rejected':
                    returnClass = 'px-2 text-center bg-warning text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        FetchData: async function () {
            var vm = this;
            // vm.isLoading = true; 
            //var url =`postContent=${vm.postContent}&campaignOwnerId=${vm.campaignOwnerId}&approverId=${vm.approverId}&datefrom=null&dateto=${vm.dateto}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
            var url = '';
            await DataService.CampaignApprovalListing(url).then(response => {
                vm.ApprovalData = [];
                if (response.data != '') {
                    vm.ApprovalData = response.data;
                    if(vm.ApprovalData.length > 0){
                        vm.ApprovalData.forEach(x=>{
                            x.content = vm.removeHTMLTags(x.content);
                        })
                    }
                    vm.ApprovalData.forEach((row) => {
                        row.isCheckBoxDisabled = false;
                        if (row.status_code == 'CRM_APPROVE' || row.status_code == 'CRM_REJECT') {
                            row.isCheckBoxDisabled = true;
                        }
                        row.additionalClass = '';
                        row.status_code == 'CRM_REJECT' ? row.additionalClass = 'danger-bar' : row.additionalClass = 'warning-bar';                 
                    });
                    vm.TotalRecord = response.data[0].total;
                    vm.TotalPages = Math.ceil(vm.TotalRecord / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecord = false;
                }
                else {
                    vm.noRecord = true;
                }
            })
            setTimeout(function () {
                vm.ResponsiveDataTable('tablelistingdata')
            }, 500);
            vm.isLoading = false;
        },
        removeHTMLTags(text) {
            return text.replace(/<[^>]*>/g, '');
        },
        async GetPageList() {
            var vm = this;
            var url = 'TYPE=CRM_SOCIAL&MODULE_NAME=CRM';
            DataService.GetTriggerFieldValue(url).then(function (response) {
                vm.AllPages = response.data;
            })
        },
    
        ApproveReject: async function (PostStatus,ApproveId) {
            var vm = this;
            var val = "";
            vm.PostStatus=PostStatus;
            if (ApproveId != "") {
                val = ApproveId;
            } else {
                if (typeof (this.id) !== "undefined" && this.id != null) {
                    val = this.id;
                }
                else {
                    $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                        if (val.length > 0)
                            val += ',';
                        val += $(this).val();
                    });
                }
            }
            vm.PostId=val;
            if (val.length > 0 || val != "") {
                var url = `id=${val}`;
                await DataService.GetSocialMediaPost(url).then(function (response) {
                        if (response.data.data != null && response.data.data != "") {
                            vm.resultData = response.data.data[0];
                        }
                    });
                vm.confirmR(vm.$t("AreYouSureToApproveSelectedRecords"), true, vm.$t("Approve", [vm.$t("CampaignApproval")]), function () {
                vm.PostComment=true;
                });
            }
            else {
                vm.ShowAlert(vm.$t("LeaveApprove"), "warning", true, vm.$t("Alert"));
            }
        },
    }
}
</script>