<template>
    <div class="modal d-block my-popups dialog">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <slot name="header">
                        <h5>{{$t('AddTriggerLink')}}</h5>
                    </slot>
                    <button type="button" title="" v-on:click="$emit('close')" class="close" data-original-title="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>
                <div class="modal-body">
                    <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" />
                    <div class="col-lg-6">
                        <div class="py-2">
                            <small class="text-danger">{{ $t('MandatoryString') }}</small>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataServices from "../../../services/DataService";

export default{
    props:{
        Data:{
            type: Object
        }
    },
    data(){
        return{
            FormSchema:[
                {
                    layoutType: "double",
                    Data:[
                        {
                            astype: "TextField",
                            label: this.$t('name'),
                            name: "TriggerTitle",
                            value: "",
                            placeholder: "name",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('LinkURL'),
                            name: "TriggerLink",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Save"
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.closepopup,
                }
            ],
            TriggerId:0
        }
    },
    created(){
        var vm =this;
        if(typeof(vm.Data) !='undefined'){
            vm.TriggerId=vm.Data.TRIGGER_ID;
            vm.FormSchema[0].Data[0].value=vm.Data.TRIGGER_TITLE;
            vm.FormSchema[0].Data[1].value=vm.Data.TRIGGER_LINK;
        }
    },
    methods:{
        onSubmit(formfields){
            var vm = this;
            var obj = {
                trigger_id: vm.TriggerId,
                trigger_title: formfields.TriggerTitle,
                trigger_link: formfields.TriggerLink,
                status_id: 1001
            }           
            var url = JSON.stringify(obj);
            DataServices.SaveTriggerLink(url).then(response=>{
                if(response.data == 'INSERT'){
                    vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('TriggerLink')]), "Success", true, vm.$t("Alert"));
                    vm.$parent.FetchData()
                    vm.closepopup();
                }
                else if(response.data == 'UPDATE'){
                    vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('TriggerLink')]), "Success", true, vm.$t("Alert"));
                    vm.$parent.FetchData()
                    vm.closepopup();
                }
                else if(response.data == 'DUPLICATE'){
                    vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('TriggerLink')]), "warning", true, vm.$t("Alert"));
                }
                else{
                    vm.ShowAlert(vm.$t('AddedError',[vm.$t('TriggerLink')]),"failure", true, vm.$t("Alert"));
                }
            })
        },
        closepopup(){
        var vm = this;
        vm.$parent.ClosemodalManageTriggerLink();
    },
    }
}
</script>