<template>
<section>
    <loader :is-visible="isLoading"></loader>
    <tg-list :IsShowAction="true" ModuleName="CRM" SubModuleCode="CRM_SMS_TEMPLATE" :RenderRowActionMethod="renderActions" 
    v-on:ActionButtonClick="actionButtonClick" IdentityColumn="TEMPLATE_ID" :HeaderText="$t('Template')" :listType="['List']" 
    :ListData="TemplateData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecord" 
    :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp" 
    :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch" :CurrentPageNumber="PageNumber"
    v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata"></tg-list>
    <modalAddSMSCampaign v-if="ShowmodalAddSMSCampaign" :GetTemplateData="GetTemplateData" :reqType="reqType" @FetchData="FetchData" @close="ShowmodalAddSMSCampaign = false"></modalAddSMSCampaign>
</section>
</template>

<script>
import modalAddSMSCampaign from '../../../../components/Common/Modal/modalAddSMSCampaign.vue';
import Schema from '../SMSCampaign/SMSCampaignSchema';
import CampaignDataServices from '../../../../services/DataService';

export default {
    components: {
        modalAddSMSCampaign
    },
    data() {
        return {
            ShowmodalAddSMSCampaign: false,
            isLoading: false,
            TemplateData: [],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            TotalRecord: 0,
            SortExp: '',
            SortBy: '',
            PageNumber: 1,
            PageSize: 10,
            template_name: '',
            privilegeParams: null,
            TotalPages: 0,
            CurrentPage: 1,
            GetTemplateData: {},
            Headers: [{
                    COLUMN_NAME: "TEMPLATE_NAME",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Name",
                    DISPLAY_ORDER: 1
                },
                {
                    COLUMN_NAME: "TEMPLATE_CONTENT",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Description",
                    DISPLAY_ORDER: 2
                },
                {
                    COLUMN_NAME: "TYPE",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Type",
                    DISPLAY_ORDER: 3
                },
                {
                    COLUMN_NAME: "ATTACHMENT_COUNT",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Attachment",
                    DISPLAY_ORDER: 4
                }
            ]
        };
    },
   async created() {
        var vm = this;
        await vm.FetchData();
        this.privilegeParams = `controller=CRM&action=`;
    },
    methods: {
        pagerMethod: async function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            await this.FetchData();
        },
         sortdata: function (s) {
          var SortDir = "sort tb_headerSortDown";
           this.SortBy = s;
                if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else
                {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                }
               
                this.FetchData();
                $("#" + s+'  span:first-child').attr("class", SortDir);
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "modalAddSMSCampaign":
                    this.modalAddSMSCampaign(0);
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        actionButtonClick: function (callbackfunction, item, index) {
            var vm = this;
            switch (callbackfunction) {
                case 'modalAddSMSCampaign':
                    vm.modalAddSMSCampaign(item);
                    break;
                case 'Delete':
                    vm.commonDeleteData(item.TEMPLATE_ID);
                    break;
            }
        },
        modalAddSMSCampaign: function (value) {
            if(value.TEMPLATE_ID > 0){
                this.reqType='Update';
                this.GetTemplateData=value; 
            }else{
                this.GetTemplateData = {};
            }
            this.ShowmodalAddSMSCampaign = true;
        },
        ClosemodalAddSMSCampaign: function () {
            this.ShowmodalAddSMSCampaign = false;
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        FetchData: async function () {
            var vm = this;
            vm.isLoading = true; 
            var url = `templateName=${vm.template_name}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
            await CampaignDataServices.SMSTemplateListing(url).then(function (response) {
                vm.isLoading=false;
                if (typeof (response.data.data) != 'undefined' && response.data.data != null){
                    vm.TemplateData = response.data.data;
                    vm.TotalRecord = response.data.data[0].Total;
                    vm.TotalPages = Math.ceil(vm.TotalRecord / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecord = false;
             } else 
                {
                    vm.noRecord = true;
                    vm.TemplateData = [];
                    vm.TotalRecord = 0;
                }
            });
            setTimeout(function () {
                vm.ResponsiveDataTable('tablelistingdata')
            }, 500);
            vm.isLoading = false;  
        },
        commonDeleteData: function (TEMPLATE_ID) {
            var vm = this;
            CampaignDataServices.CheckPrivilege(vm.privilegeParams + 'Delete')
                .then(function (response) {
                    if (response.data) {
                        var val = "";
                        if (typeof (TEMPLATE_ID) != 'undefined') {
                            val = TEMPLATE_ID.toString();
                        } else {
                            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                                if (val.length > 0)
                                    val += ',';
                                val += $(this).val();
                            });
                        }
                        if (val.length > 0) {
                            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Campaign")), function () {
                                var postJSON = {
                                    ids: val,
                                    moduleName: "CRM",
                                    subModuleCode: "CRM_SMS_TEMPLATE"
                                };
                                var objectPostString = JSON.stringify(postJSON);
                                CampaignDataServices.CommonDeleteData(objectPostString).then(function (response) {
                                    var box = "";
                                    if (response.status == 200) {
                                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                        $("#DeleteMultiple").addClass("disabled");
                                        box = vm.ShowAlert(vm.$t('DeletedSuccessfully', [vm.$t('Campaign')]), "success", true, vm.$t("Alert"));
                                        vm.PageNumber = parseInt(vm.PageNumber) - 1;
                                        if (vm.PageNumber <= 0) {
                                            vm.PageNumber = 1;
                                        }
                                        vm.FetchData();
                                    } else {
                                        box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Campaign')]), "failure", true, vm.$t("Alert"));
                                    }
                                    setTimeout(function () {
                                        box.modal('hide');
                                    }, 2000);

                                });
                            });
                        } else {
                            vm.ShowAlert(vm.$t('Selectanyrecordtodelete'), "warning", true, vm.$t("Alert"));
                        }
                    } else {
                        vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                    }

                });
        },
    },
}
</script>
