<script>
import DataServices from "../../../services/DataService";
export default {
    props: {
        PostId: {
            type: Number,
            required: true
        },
        ApproverId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            attachmentPath: '',
            blobPath: 'https://cogniterblob.blob.core.windows.net/',
            containerName: '',
            PostContent: '',
            ApproverNote: '',
            modified_at: '',
            formattedDate: '',
            PageName:'',
            PageId: '',
            statusClass:'',
            ApprovalData: []
        }
    },
    async created() {
        var vm = this;
        vm.UserId = vm.GetUserInfo.ID;
        if(vm.UserId == vm.ApproverId){
            vm.statusClass='';
        }else{
            vm.statusClass='disabled';
        }
        vm.containerName = vm.GetUserInfo.ContainerName;
        await vm.FetchData();
    },
    methods: {
        async FetchData() {
            var vueObj = this;
            var url = `id=${vueObj.PostId}`;
            await DataServices.GetSocialMediaPost(url).then(
              async  function (response) {
                    if (response.data != null && response.data != "") {
                        if (response.data.data.length >= 1) {
                            if(response.data.data[0].status_code=='CRM_APPROVE' || response.data.data[0].status_code=='CRM_REJECT'){
                                vueObj.statusClass='disabled';
                            }
                            var attachmentPath = response.data.data[0].attachement_path;
                            if (attachmentPath) {
                                vueObj.attachmentPath = vueObj.blobPath + vueObj.containerName + '/' + attachmentPath;
                                }
                                vueObj.ApprovalData=response.data;
                                vueObj.PageId=response.data.data[0].ref_page_id;
                                vueObj.ApproverNote = response.data.data[0].note;
                                var data = response.data.data[0].content;
                                if (data.includes("<p>")) {
                                    vueObj.PostContent = data.replace("<p>", "").replace("</p>", "")
                                } else {
                                    vueObj.PostContent = response.data.data[0].content;
                                }
                                var created_at = response.data.data[0].created_at;
                                if (created_at !='') {
                                    const formattedDateTime = vueObj.formatDateforPost(created_at);
                                    vueObj.formattedDate = formattedDateTime;
                                }
                                await vueObj.GetPageList();
                        }
                    }
                }
            );
        },
        Close: function () {
            this.$parent.OpenPostModal = false;
        },
        formatDateforPost: function (datetimeString) {
            const months = [
                "January", "February", "March", "April", "May", "June", "July",
                "August", "September", "October", "November", "December"
            ];
            const date = new Date(datetimeString);
            const day = date.getDate();
            const month = months[date.getMonth()];
            const year = date.getFullYear();
            const hours = date.getHours();
            const minutes = date.getMinutes();
            const amOrPm = hours >= 12 ? 'PM' : 'AM';
            const formattedDate = `${day} ${month} at ${hours}:${minutes < 10 ? '0' : ''}${minutes} ${amOrPm}`;
            return formattedDate;
        },
        async GetPageList(){
            var vm=this;
            var url = 'TYPE=CRM_SOCIAL&MODULE_NAME=CRM';
            DataServices.GetTriggerFieldValue(url).then(function (response) {
                vm.AllPages = response.data;
                if(vm.AllPages !=null && vm.AllPages !=undefined){
                    vm.AllPages.forEach(item=>{
                        if(item.PAGE_ID==vm.PageId){
                            vm.PageName=item.PAGE_NAME;
                        }
                    })
                }
            })
        },
        ApproveRejectPost: function (status) {
            var vm = this;
            vm.$emit('ApproveReject', status, vm.PostId);
        },
    }
}
</script>

