<template lang="en">
<div class="modal d-block my-popups dialog">
    <div class="modal-dialog">
        <div class="modal-content">
            <div class="modal-header">
                <slot name="header">
                    <h5>{{$t('TriggerLink')}}</h5>
                </slot>
                <button type="button" title="" v-on:click="$emit('close')" class="close" data-original-title="Close">
                    <em class="fa fa-times" aria-hidden="true"></em>
                </button>
            </div>
            <div class="modal-body">
                <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" />
                <div class="pl-0 border-1">
                    <v-date-picker is-range class="timedate input-group col-md-3 float-right" v-model="startEndDate" @input="onDateChange">
                     <template v-slot="{ inputValue, inputEvents, togglePopover }">
                       <input class="form-control" :value="inputValue.start + ' - ' + inputValue.end" v-on="inputEvents" />
                         <div class="input-group-append" v-on:click="togglePopover">
                        <span class="input-group-text">
                          <em class="fa fa-calendar"></em>
                        </span>
                         </div>
                     </template>
                    </v-date-picker> 
                </div> 
                <div class="col-md-12 float-left p-4 text-center">    
                    <div id="chart">
                        <apexcharts height="350" v-if="ischartvisibility" type="bar" :options="chartOptions" :series="series"></apexcharts>
                    </div>
                </div>
                <div class="col-md-12 float-left p-0 mt-3">
                    <div class="heading-border">
                        <h5 class="d-inline-block"><span>{{$t('TriggerLink')}}</span></h5>
                    </div>
                </div> 
                <loader :is-visible="isLoading"></loader>
                <tg-list :IsShowAction="false" ModuleName="CRM" SubModuleCode="" :RenderRowActionMethod="renderActions" 
                    v-on:ActionButtonClick="actionButtonClick" IdentityColumn="" :HeaderText="$t('TriggerLink')" :listType="['List']" 
                    :ListData="StatisticsData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecord" 
                    :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData" :showCheckBox="false" :SortExp="SortExp" 
                    :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch" :CurrentPageNumber="PageNumber"
                    v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                </tg-list>
            </div>
        </div>
    </div>
</div>
</template>

    
<script>
import DataService from "../../../services/DataService";
import VueApexCharts from "vue-apexcharts";

export default {
    components:{
        apexcharts: VueApexCharts,
    },
    data() {
        return {
            startEndDate: {
                start: new Date(),
                end: new Date(),
            },
            chartOptions: {
              chart: {
                id: 'basic-bar'
            },
            xaxis: {
                categories: []  
              }
            },
            series: [{
              name: 'series-1',
              data: [] 
            }],
            listheaderbuttons: '',
            leftSearchFields: '',
            leftCommonSearch:'',
            listActions: [],
            TotalRecord: 0,
            StatisticsData:[],
            SortExp: '',
            SortBy: '',
            PageNumber: 1,
            PageSize: 10,
            TotalPages: 0,
            CurrentPage: 1,
            FormSchema:'',
            ischartvisibility: false,
            isLoading: false,
            triggerTitle: '',
            datefrom: new Date(),
            dateto: new Date(),
            Headers: [{
                    COLUMN_NAME: "TRIGGER_TITLE",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Name",
                    DISPLAY_ORDER: 1
                },
                {
                    COLUMN_NAME: "TOTAL_CLICKS",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Clicks",
                    DISPLAY_ORDER: 2
                }
            ]
        }
    },
    created() {
        var vm = this;
        vm.FetchData();
    },
    methods: {
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (s) {
          var SortDir = "sort tb_headerSortDown";
           this.SortBy = s;
                if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else
                {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                }
                this.FetchData();
                $("#" + s+'  span:first-child').attr("class", SortDir);
        },
        commonHeaderButtonClick: function () {
        },
        actionButtonClick: function () {
        },
        renderActions: function () {
        },
        FetchData: function(){
            var vm = this;
            vm.isLoading = true; 
            vm.ischartvisibility=false;
            var url = `triggerTitle=&datefrom=${vm.$options.filters.formatDate(vm.datefrom)}&dateto=${vm.$options.filters.formatDate(vm.dateto)}`;
            DataService.GetTriggerLinkStatistics(url).then(response =>{
                vm.StatisticsData=[];
                if(response.data != ''){
                    vm.StatisticsData = response.data;
                    vm.StatisticsData.forEach(element => {
                        vm.chartOptions.xaxis.categories.push(element.CLICK_DATE);
                        vm.series[0].data.push(element.TOTAL_CLICKS);
                    });
                    vm.ischartvisibility=true;
                }
                vm.isLoading = false;  
            })
        },
        onDateChange: function(){
            var vm = this;
            vm.datefrom = vm.startEndDate.start;
            vm.dateto = vm.startEndDate.end;
            vm.FetchData();
        }
    }
}
</script>
